<template>
  <PageWrapper dense contentFullHeight fixedHeight contentClass="flex" style="position: relative">
    <!-- 组织树 -->
    <caterogry @select-tree="selectTree" />
    <!-- 数据表格 -->
    <div class="right-table-layout">
      <BasicTable
        @resize-column="(w, col) => (col.width = w)"
        @register="registerTable"
        :searchInfo="searchInfo"
        @selection-change="handleSelectChange"
        :resizeHeightOffset="32"
      >
        <template #toolbar>
          <a-button type="primary"   :preIcon="IconEnum.ADD" @click="handleCreate">新增</a-button>
          <a-button type="primary" :disabled="selectedRowId == ''" :preIcon="IconEnum.EDIT" @click="handleUpdate">修改</a-button>
          <a-button class="red-btn" :disabled="selectedRowId == ''" :preIcon="IconEnum.DELETE" @click="handleDelete">删除</a-button>
          <a-button class="yellow-btn"  :preIcon="IconEnum.EXPORT" @click="handleExport">导出</a-button>
          <a-button class="green-btn" :disabled="selectedRowId == ''" :preIcon="IconEnum.ENABLE" @click="handleEnable">启用</a-button>
          <a-button class="grey-btn" :disabled="selectedRowId == ''" :preIcon="IconEnum.DISABLE" @click="handleDisable">禁用</a-button>
        </template>
        <template #bodyCell="{ column, text }">
          <template v-if="column.key === 'status'">
            <span style="color: #19be6b" v-if="text == 1">启用</span>
            <span style="color: red" v-if="text == 0">禁用</span>
          </template>
        </template>
      </BasicTable>
    </div>
    <!-- 新增弹窗 -->
      <Dialog v-model:visible="open" 
      smode="default" 
      :title="openTitle" 
      :width="600"
      :height="400"
      @ok="handleSuccess"
      okText="确定"
      @cancel="handleCancel"
      cancelText="取消"
      >
        <a-form :model="formState" 
        name="createMaterial" 
        labelAlign="center" 
        ref="formStateRef" 
        
        v-bind="formItemLayout">
          <a-row :gutter="32">
            <a-col :span="24">
              <a-form-item
                label="物料类型"
                name="fullName"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: false, message: '请选择物料类别' }]"
              >
                <a-input v-model:value="formState.fullName" disabled />
              </a-form-item>
            </a-col>
            <a-col :span="24">
              <a-form-item
                label="物料编码"
                name="code"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: false, message: '请选择物料编码' }]"
              >
                <a-input v-model:value="formState.code" disabled />
              </a-form-item>
            </a-col>
            <a-col :span="24">
              <a-form-item
                label="物料名称"
                name="name"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '请输入物料名称' }]"
              >
                <a-input v-model:value="formState.name" />
              </a-form-item>
            </a-col>
            <a-col :span="24">
              <a-form-item
                label="计量单位"
                name="unit"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '请选择计量单位' }]"
              >
                <DictSelectBox
                  :type="`safety_tpl_unit`"
                  @change="handleDictSelectBoxChange"
                  v-model:value="formState.unit"
                  style="width: 100%"
                />
              </a-form-item>
            </a-col>
            <a-col :span="24">
              <a-form-item
                label="质保期(月)"
                name="warrantyMonth"
                :labelCol="{ style: { width: '124px'} }" 
                :rules="[{ required: true, message: '请输入质保期(月)' }]"
              >
                <a-input-number v-model:value="formState.warrantyMonth" :min="1" :max="100000" />
              </a-form-item>
            </a-col>
          </a-row>
          <!-- <template #footer>
            <a-button key="submit" type="primary" @click="handleSuccess" >确定</a-button>
            <a-button key="back" @click="handleCancel">取消</a-button>
          </template> -->
        </a-form>
      </Dialog>
  </PageWrapper>
</template>
<script lang="ts" setup>
  import { onMounted, reactive, ref } from 'vue';
  import { BasicTable, useTable,SorterResult } from '/@/components/Table';
  import { PageWrapper } from '/@/components/Page';
  import caterogry from '/@/views/baseset/operation/material/materialCategory.vue';
  import Dialog from '@/components/Framework/Modal/Dialog.vue';
  import { IconEnum } from '@/enums/appEnum';
  import { message } from 'ant-design-vue';
  import DictSelectBox from '@/components/Framework/Combox/DictSelectBox.vue';
  import { useMessage } from '@/hooks/web/useMessage';
  import { exportExcelFile } from '@/utils/file/download';
  import { useI18n } from '@/hooks/web/useI18n';
  import {
    getMaterialDataApi,
    addMaterialDataApi,
    updateMaterialDataApi,
    deleteMaterialDataApi,
    updateStatusApi,
    exportMaterialData,
    columns,
  } from './material';

  const { t } = useI18n();
  const { createConfirm, createMessage } = useMessage();
  let selectedRowId = ref('');
  const searchInfo = reactive<any>({
    sortField : 'code',
    sortOrder : 'desc'
  }); // 查询信息
  const selectedNode:any = ref({}); // 被选择的节点
  const openTitle=ref('');
  const formStateRef=ref();
  const open = ref<boolean>(false);
  const loadingFlag = ref<boolean>(false);

  const formState:any = reactive({
    id: '',
    name: '',
    code: '',
    status: '',
    unit: '',
    materialCategoryId: '',
    fullName: '',
    warrantyMonth: '',
  });
 
  const getDataListParam = async () => {
    clearSelectedRowKeys();
    let param = searchInfo;
    const paginationRef = getPaginationRef() as any;
    param.pageNo = paginationRef.current;
    param.pageSize = paginationRef.pageSize;
    let dataObj = await getMaterialDataApi(param);
    return dataObj.result;
  };

  const [registerTable, { reload, getSelectRows, getSelectRowKeys, getPaginationRef, clearSelectedRowKeys }] = useTable({
    title: '物料列表',
    api: getDataListParam,
    rowKey: 'id',
    columns,
    useSearchForm: true,
    formConfig: {
      labelWidth: 120,
      showAdvancedButton:false,
      schemas: [
        { label: '物料名称', field: 'name', component: 'Input', colProps: { span: 8 } },
        { label: '物料编码', field: 'code', component: 'Input', colProps: { span: 8 } },
        {
          label: '状态',
          field: 'status',
          component: 'DictSelectBox',
          componentProps: {
            type: 'enable_or_disable',
          },
          colProps: { span: 8 },
        },
      ],
    },
    showTableSetting: true,
    bordered: true,
    pagination: true,
    rowSelection: {
      type: 'radio',
      fixed: true,
      onSelect: onSelect
    },
    sortFn: (sortInfo: SorterResult) => {
      if (sortInfo.order) {
        searchInfo.sortField = sortInfo.field.replace('Text', '');
        searchInfo.sortOrder = sortInfo.order.replace('end', '');
      } else {
        searchInfo.sortField = 'code';
        searchInfo.sortOrder = 'desc';
        }
    },
    handleSearchInfoFn(info) {
      searchInfo.code = info.code;
      searchInfo.name = info.name;
      searchInfo.status = info.status;
      return info;
    },
  });

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 4 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 20 },
    },
  };

  const handleCancel = () => {
    open.value = false;
    resetForm();
  };

  function handleSelectChange({ keys, rows }) {
    if (rows && rows.length > 0) {
      const record = rows[0];
      selectedRowId.value = record.id;
    } else {
      selectedRowId.value = '';
    }
  }
  function onSelect(record, selected) {
    if (selected) {
      selectedRowId.value = record.id;
    } else {
      selectedRowId.value = '';
    }
  }

  function handleDictSelectBoxChange(value) {
    formState.unit = value;
  }

  function resetForm() {
    formState.id = '';
    formState.name = '';
    formState.code = '';
    formState.status = '';
    formState.unit = '';
    formState.materialCategoryId = '';
    formState.fullName = '';
    formState.warrantyMonth = '';
  }

  function handleCreate() {
    if (!selectedNode.value.id) {
      message.info('请选择分类');
      return false;
    }
    // 判断是否还有子节点,有则不能新增物料
    if (selectedNode.value.hasChildren) {
      message.info('有子节点,不能新增物料');
      return false;
    }
    formState.fullName = selectedNode.value.fullName;
    formState.code = '';
    formState.name = '';
    formState.unit = '';
    formState.warrantyMonth = '';
    formState.materialCategoryId = selectedNode.value.id;
    openTitle.value='新增物料';
    open.value = true;
  }

  function handleUpdate() {
    let rows = getSelectRows();

    if (rows.length !== 1) {
      message.info('请选择一行数据');
      return;
    }
    let selectedRow = rows[0];
    formState.id = selectedRow.id;
    formState.code = selectedRow.code;
    formState.name = selectedRow.name;
    formState.materialCategoryId = selectedRow.materialCategoryId;
    formState.fullName = selectedRow.fullName;
    formState.status = selectedRow.status;
    formState.unit = selectedRow.unit;
    formState.warrantyMonth = selectedRow.warrantyMonth;
    openTitle.value='修改物料';
    open.value = true;
  }

  function handleDelete() {
    let rowKeys = getSelectRowKeys();
    if (rowKeys.length != 1) {
      message.info('请选择一行数据');
      return;
    }

    createConfirm({
      title: '提示',
      iconType: 'warning',
      content: "确定要删除选中的记录？",
      async onOk() {
        deleteMaterialDataApi(`?id=${rowKeys[0]}`).then((rsp) => {
          if (rsp.code == '0') {
            message.success('数据已删除。');
            reload();
          } else {
            message.success('数据删除失败。');
          }
          open.value = false;
        });
      },
    });
    
  }

  function handleEnable() {
    let rowKeys = getSelectRowKeys();
    if (rowKeys.length != 1) {
      message.info('请选择一行数据');
      return;
    }
    updateStatusApi({ id: rowKeys[0], status: 1 }).then((rsp) => {
      if (rsp.code == '0') {
        message.success('启用成功。');
        reload();
      } else {
        message.success('启用失败。');
      }
      loadingFlag.value = false;
    });
  }
  function handleDisable() {
    let rowKeys = getSelectRowKeys();
    if (rowKeys.length != 1) {
      message.info('请选择一行数据');
      return;
    }
    updateStatusApi({ id: rowKeys[0], status: 0 }).then((rsp) => {
      if (rsp.code == '0') {
        message.success('禁用成功。');
        reload();
      } else {
        message.success('标用失败。');
      }
      loadingFlag.value = false;
    });
  }

  function handleExport() {
    createConfirm({
      title: t('common.exportTitle'),
      iconType: 'warning',
      content: t('common.exportMessage'),
      async onOk() {
        await exportMaterialData(searchInfo).then((res) => {
          exportExcelFile(res?.data, '物料xls');
        });
        createMessage.success(t('common.exportSuccessText'));
      },
    });
  }

  function selectTree(data) {
    if (data) {
      selectedNode.value = data;
      searchInfo.materialCategoryCode = data.code;
    } else {
      selectedNode.value = {};
      searchInfo.materialCategoryCode = '';
    }
    reload();
  }

  // 数据生成成功回调函数
  const handleSuccess=()=> {
    let formRef = formStateRef.value;
    formRef.validateFields().then(async ()=>{
      loadingFlag.value = true
      if (formState.id == '') {
      let rsp = await addMaterialDataApi(formState) as any;
        if (rsp.code == 0) {
            message.success('数据已生成。');
            reload();
            open.value = false;
            resetForm();
          } else {
            message.error('数据生成失败。');
          }
    } else {
      let rsp = await updateMaterialDataApi(formState) as any;
      if (rsp.code == 0) {
        message.success('数据已更新。');
        reload();
        open.value = false;
        resetForm();
      } else {
        message.error('数据更新失败。');
      }
    }
    loadingFlag.value = false;
  }).catch((e)=>{
    console.log(e);
    loadingFlag.value = false;
  })
}

  // 启动执行代码
  onMounted(() => {});
</script>
<style lang="less" scoped>
  .dialog-loading {
    position: absolute;
    z-index: 1000 !important;
    top: 45%;
    left: 45%;
  }

  .dialog-mask {
    position: absolute;
    z-index: 1001 !important;
    top: -1px;
    width: 100%;
    height: 100%;
    opacity: 0.3;
    background: #ccc;
  }

  .right-table-layout {
    flex: 1;
    width: calc(100% - 308px);
  }
</style>
