import { BasicColumn } from '/@/components/Table';
import { defHttp } from '/@/utils/http/axios';

enum Api {
  getMaterialData = '/baseset/material/page',
  addMaterialData = '/baseset/material/create',
  updateMaterialData = '/baseset/material/update',
  deleteMaterialData = '/baseset/material/delete',
  updateStatus = '/baseset/material/updateStatus',
  exportMaterialData = '/baseset/material/export-excel',
}

// 列表表头
export const columns: BasicColumn[] = [
  { key: 'fullName', title: '物料类型', dataIndex: 'fullName', align: 'left',headAlign:'center', width: 120,sorter: true,resizable: true },
  { key: 'code', title: '物料编码', dataIndex: 'code', align: 'left',headAlign:'center', width: 120,sorter: true,resizable: true },
  { key: 'name', title: '物料名称', dataIndex: 'name', align: 'left',headAlign:'center', width: 120,sorter: true,resizable: true },
  { key: 'unitText', title: '计量单位', dataIndex: 'unitText', align: 'center', width: 120,sorter: true,resizable: true },
  {
    key: 'warrantyMonth',
    title: '质保期(月)',
    dataIndex: 'warrantyMonth',
    align: 'right',headAlign:'center',
    width: 120,sorter: true,resizable: true,
  },
  { key: 'status', title: '状态', dataIndex: 'status', align: 'center', width: 120,sorter: true,resizable: true },
];

// 日期详细数据表头字段获取方法
export function getColumns(): BasicColumn[] {
  return columns;
}

export interface DeptListItem {
  id: string;
  materialCategoryId: string;
  name: string;
  code: string;
  unit: number;
  warrantyMonth: number;
  status: number;
}

// 列表数据
export const getMaterialDataApi = (params) =>
  defHttp.get(
    {
      url: Api.getMaterialData,
      params,
    },
    {},
  );

// 新增
export const addMaterialDataApi = (params) =>
  defHttp.post(
    {
      url: Api.addMaterialData,
      params,
    },
    {},
  );

// 修改
export const updateMaterialDataApi = (params) =>
  defHttp.put(
    {
      url: Api.updateMaterialData,
      params,
    },
    {},
  );
// 修改状态
export const updateStatusApi = (params) =>
  defHttp.put(
    {
      url: Api.updateStatus,
      params,
    },
    {},
  );

// 删除
export const deleteMaterialDataApi = (params) =>
  defHttp.delete(
    {
      url: Api.deleteMaterialData,
      params,
    },
    {},
  );

// 导出
export const exportMaterialData = (params) =>
  defHttp.download(
    {
      url: Api.exportMaterialData,
      params,
    },
    {},
  );
